import util from '@/libs/util'

export default {
    namespaced: true,
    state: {
        many: {
            // 
            pay: false,
            // 
            ShopVideoBacActions: false,
            // app
            BarHeight: 0,
            // 
            ShopVideoBacShare: false,
            // 
            ShopVideoBacPoster: false,
            // 
            footerHeight: 0,
            // 
            IsShopBackend: false,
            // 
            ShopVideoXcxShare: {
                //  wap  xcx  all  
                site: '',
                // 
                xcx: {}
            },
            // 
            ShopVideoShareSetting: {},
            // 
            VideoData: {}
        }
    },

    // 
    mutations: {
        /**
         * set
         * @param state
         * @param data
         * @constructor
         */
        SetData(state, data = {}) {
            setTimeout(function () {
                state.many[data.key] = data.val;
            });
        },

        /**
         * set 
         * @param state
         * @param data
         * @constructor
         */
        SetBatchData(state, data = {}) {
            setTimeout(function () {
                data.forEach(function (value, index, array) {
                    state.many[value.key] = value.val;
                });
            });
        }
    },

    // 
    actions: {
        /**
         * set
         * @param context
         * @param data
         * @constructor
         */
        Set(context, data = {}) {
            context.commit('SetData', data);
        },

        /**
         * set 
         * @param context
         * @param data
         * @constructor
         */
        SetBatch(context, data = {}) {
            context.commit('SetBatchData', data);
        }
    }
}



