import router from '@/router'
import store from '@/store'
import util from '@/libs/util'
import Hint from '@/plugins/hint'
import {weixinShare} from "@/api/shop/video.frontend"
import wx from 'weixin-js-sdk'

/**
 * js-sdk
 * https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
 */

const wechat = {
    /**
     * 
     * @param callback
     * @param url
     * @param name rewrite ^(.*)/([0-9a-zA-Z_-]+)/wap_pro/([0-9]+)\.html$ $1/V/index.html?username=$2&pro_id=$3 last;
     */
    share(callback, url, name,type) {
        let getData = ''
        // 
        weixinShare({
            username: name,
            url: url
        }).then(res => {
            getData = res.signPackage;
            wx.config({
                debug: false, // 
                appId: res.signPackage.appId, // 
                timestamp: res.signPackage.timestamp, // 
                nonceStr: res.signPackage.nonceStr, // 
                signature: res.signPackage.signature, // 1
                jsApiList: [
                    "onMenuShareTimeline", "onMenuShareAppMessage", "onMenuShareQQ",'checkJsApi','getLocation', 'openLocation','openAddress'
                ] // JSJS2
            })
        });

        wx.ready(() => {
            if(type == 1){
                // ready
                if (callback) {
                    callback(getData)
                }
            }else if(type == 2){//
                wx.openLocation({
                    latitude : callback[3], // 90 ~ -90
                    longitude : callback[2], // 180 ~ -180
                    name : callback[0] ? callback[0] : '', // 
                    address : callback[1], // 
                    scale : 28, // ,,1~28
                    infoUrl : 'http://www.gongjuji.net' // ,
                });
            }else if(type == 3){//
                wx.ready(function() {
                    wx.openAddress({
                        success: function(res) {
                            if (callback) {
                                callback(res)
                            }
                        },
                        fail: function(err) {
                            alert(err.errMsg)
                        }
                    });
                });
            }

        })
    },

    /**
     * [ShareTimeline ]
     * @param {[type]} option []
     * @param {[type]} success []
     * @param {[type]} error   []
     */
    ShareTimeline(option) {
        wx.onMenuShareTimeline({
            title: option.title, // 
            link: option.link, // 
            imgUrl: option.imgUrl, // 
            success() {
                // 
                option.success()
            },
            cancel() {
                // 
                option.error()
            }
        })
    },

    /**
     * [ShareAppMessage ]
     * @param {[type]} option []
     * @param {[type]} success []
     * @param {[type]} error   []
     */
    ShareAppMessage(option) {

        wx.onMenuShareAppMessage({
            title: option.title, // 
            desc: option.desc, // 
            link: option.link, // 
            imgUrl: option.imgUrl, // 
            success() {
                // 
                option.success()
            },
            cancel() {
                // 
                option.error()
            }
        })
    }
};

export default wechat



