import store from '@/store'

export default {
    // 
    unNProgress: false,

    // error
    AxiosErrorPopup: true,

    // formdata
    ISFormData: false,

    /**
     * 
     * @param data
     * @constructor
     */
    Set(data = {}) {
        store.dispatch("variables/Set", data)
    },

    /**
     *  
     * @param data
     * @constructor
     */
    Setbatch(data = {}) {
        store.dispatch("variables/SetBatch", data)
    },
}



