export function showProList(data) {
    util.variable.unNProgress = true;
    data.real_host = location.host;
    return ajax({
        url: '/mallorder/api/short_video_pro_list.php',
        method: 'post',
        data: {...{type:14, front: 0}, ...data}
    })
}


