import request from '@/plugins/axios'
import util from '@/libs/util'

/**
 * 
 * @constructor
 */
export function login(data) {
    return request({
        url: '/index/shopLogin',
        method: 'post',
        data
    })
}

/**
 * oss
 * @constructor
 */
export function ossconf(data) {
    return request({
        url: '/shop/video/oss',
        method: 'post',
        data
    })
}

/**
 * 
 * @constructor
 */
export function AliOSSCert(data) {
    return request({
        url: '/shop/video/aliosscert',
        method: 'post',
        data
    })
}



