// import store from '@/store'
import axios from 'axios'
import Hint from '@/plugins/hint'
import util from '@/libs/util'
import store from '@/store'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import Qs from 'qs'

// 
function errorLog(error) {
    // 
    if (process.env.NODE_ENV === 'development') {
        console.log(error)
    }
    // 
    Hint.Msg({
        message: error.message
    });
}

//  axios 
const service = axios.create({
    baseURL: process.env.VUE_APP_APP_API,
    timeout: 10000 * 500, // 
    transformRequest: [
        function (data) {
            data = data || {};
            data.timestamp = (new Date()).valueOf();
            // formdata 
            if (util.variable.ISFormData) {
                util.variable.ISFormData = false;
                return data;
            } else {
                return Qs.stringify(data);
            }
        }
    ]
});

// 
service.interceptors.request.use(
    config => {
        // 
        if (!util.variable.unNProgress) {
            NProgress.start()
        }
        // store.dispatch('frontend/loading/showLoading')

        config.headers['TOKEN'] = util.cookies.get(process.env.VUE_APP_TOKEN) || '';
        config.headers['USERNAME'] = util.cookies.get('username') || '';
        config.headers['EV_MANAGER'] = util.cookies.get('ev_manager') || '';
        config.headers['Content-Type'] = 'application/x-www-form-urlencoded';

        if (Object.prototype.toString.call(config.data) === '[object Object]') {
            config.data.cacheClear = util.functions.GetQueryString("cacheClear") || 0;
        }

        return config
    },
    error => {
        // 
        console.log(error)
        NProgress.done()
        // store.dispatch('frontend/loading/hideLoading')
        return Promise.reject(error)
    }
)

// 
service.interceptors.response.use(
    response => {
        // 
        NProgress.done()
        // store.dispatch('frontend/loading/hideLoading')
        // 
        util.variable.unNProgress = false;

        const dataAxios = response.data
        // 
        const {code} = dataAxios
        if (code === undefined) {
            // return dataAxios
            if (util.variable.AxiosErrorPopup) {
                errorLog({message: ', '})
            }

            return Promise.reject(dataAxios)
        } else {
            switch (code * 1) {
                // success
                case 200:
                    return dataAxios.data
                default:
                    // 
                    if (
                        util.functions.InArray(code * 1, [2003, 4000])
                        && store.getters.variables.many.IsShopBackend
                    ) {
                        Hint.Alert({
                            title: '',
                            message: ' '
                        }).then(() => {
                            util.app.Shop.Login();
                        });

                        return Promise.reject(dataAxios)
                    }

                    if (util.variable.AxiosErrorPopup) {
                        errorLog({
                            message: dataAxios.msg
                        })
                    }

                    util.variable.AxiosErrorPopup = true;
                    return Promise.reject(dataAxios)
            }
        }
    },
    error => {
        NProgress.done()
        // 
        util.variable.unNProgress = false;

        // store.dispatch('frontend/loading/hideLoading')
        if (error && error.response) {
            switch (error.response.status) {
                case 400:
                    error.message = ''
                    break
                case 401:
                    error.message = ''
                    break
                case 403:
                    error.message = ''
                    break
                case 404:
                    error.message = `: ${error.response.config.url}`
                    break
                case 408:
                    error.message = ''
                    break
                case 500:
                    error.message = ''
                    break
                case 501:
                    error.message = ''
                    break
                case 502:
                    error.message = ''
                    break
                case 503:
                    error.message = ''
                    break
                case 504:
                    error.message = ''
                    break
                case 505:
                    error.message = 'HTTP'
                    break
                default:
                    break
            }
        }

        errorLog(error)
        return Promise.reject(error)
    }
)

export default service



