import util from '@/libs/util'
import Hint from '@/plugins/hint'
import { Indent } from "@/api/shop/pay"

/**
 *   
 * @demo
 *       h5
 *      opeid pay.wechat.h5.init()
 *       pay.wechat.h5.run({
 *          success() {
 *              ...coding
 *          },
 *          failed() {
 *              ...coding
 *          },
 *          indent: {
 *              
 *          }
 *      })
 *
 *      app 
 *      pay.app.run({
 *          // alipay app wechat app  [1  4 app 5 app]
 *          pay_type: "alipay",
 *          // id
 *          id: 1,
 *          // 
 *          year: 'month',
 *          // 
 *          callback: function(res) {
 *              console.log(res)
 *          }
 *      });
 * @type {{wechat: {app(), h5: {init(): void, jsApiCall(*, *=): void, run(*=): void}}, ali: {}}}
 */

const pay = {
    wechat: {
        /**
         *  js
         */
        h5: {
            /**
             * jsApiCallback
             */
            jsApiCall(conf, params = {indent: {}, success: null, failed: null}) {
                WeixinJSBridge.invoke(
                    'getBrandWCPayRequest',
                    conf,
                    function (res) {
                        if (res.err_msg == 'get_brand_wcpay_request:ok') {
                            // 
                            typeof params.success === "function" && params.success(res)
                        } else {
                            typeof params.failed === "function" && params.failed(res)
                        }
                    }
                );
            },

            /**
             * openID
             */
            init() {
                if (!util.functions.GetQueryString('openid')) {
                    location.href = "//" + location.host + "/getopenid.php?redirect=" + encodeURI(location.href);
                }
            },

            /**
             * 
             * @param params
             */
            run(params = {}) {
                // js
                $.getScript("//res.wx.qq.com/open/js/jweixin-1.4.0.js", function (response, status) {
                    Indent({
                        ...params.indent,
                        openid: util.functions.GetQueryString('openid')
                    }).then(data => {
                        util.variable.Set({
                            key: "pay",
                            val: false
                        });

                        if (!data) {
                            return Hint.Msg({
                                message: ""
                            });
                        }

                        if (typeof WeixinJSBridge == "undefined") {
                            if (document.addEventListener) {
                                document.addEventListener(
                                    'WeixinJSBridgeReady',
                                    function (data) {
                                        pay.wechat.h5.jsApiCall(data, params)
                                    },
                                    false
                                );
                            } else if (document.attachEvent) {
                                document.attachEvent(
                                    'WeixinJSBridgeReady',
                                    function (data) {
                                        pay.wechat.h5.jsApiCall(data, params)
                                    }
                                );
                                document.attachEvent(
                                    'onWeixinJSBridgeReady',
                                    function (data) {
                                        pay.wechat.h5.jsApiCall(data, params)
                                    }
                                );
                            }
                        } else {
                            pay.wechat.h5.jsApiCall(data, params);
                        }
                    }).catch(res => {
                        Hint.Alert({
                            message: JSON.stringify({res})
                        });
                    });
                });
            }
        }
    },

    /**
     * APP
     */
    app: {
        /**
         * 
         * @param params
         */
        run(params = {}) {
            util.variable.Set({
                key: "pay",
                val: true
            });
            // 
            util.variable.unNProgress = true;
            // 
            util.variable.AxiosErrorPopup = false;

            Indent({
                // alipay app wechat app  [1  4 app 5 app]
                pay_type: params.pay_type == 'wechat' ? 4 : 5,
                // 
                id: params.id,
                //  month  year 
                year: params.year,
                // 
                flow_num: params.flow_num
            }).then(data => {
                util.variable.Set({
                    key: "pay",
                    val: false
                });

                if (!data) {
                    return Hint.Msg({
                        message: data.msg
                    });
                }

                return pay.app.dispatch({
                    // 
                    content: data,
                    // 
                    callback: params.callback,
                    // 
                    pay_type: params.pay_type
                });
            }).catch(res => {
                // 
                util.variable.Set({
                    key: "pay",
                    val: false
                });

                Hint.Msg({
                    message: data.msg
                });
            });
        },

        /**
         * app
         * @param params
         * @returns {*|VanToast}
         */
        dispatch(params = {}) {
            if (!params.content) {
                return Hint.Msg({
                    message: ''
                });
            }

            if ("function" !== typeof params.callback) {
                return Hint.Msg({
                    message: ''
                });
            }

            // 
            window.payCallback = function (res) {
                params.callback(res);
            };

            let platform = util.app.Platform();
            if (params.pay_type == "alipay") {
                // 
                if (platform === 1) {
                    // 
                    pay.app.android.alipay(params);
                } else if (platform === 2) {
                    // ios
                    pay.app.ios.alipay(params);
                }
            } else if (params.pay_type == "wechat") {
                // 
                if (platform === 1) {
                    // 
                    pay.app.android.wechat(params);
                } else if (platform === 2) {
                    // ios
                    pay.app.ios.wechat(params);
                }
            }
        },

        /**
         * 
         * @constructor
         */
        android: {
            /**
             * 
             * @param params
             */
            wechat(params = {}) {
                window.android.pay(
                    2,
                    JSON.stringify({
                        content: JSON.stringify(params.content),
                        callback: "payCallback"
                    })
                );
            },

            /**
             * 
             * @param params
             */
            alipay(params = {}) {
                window.android.pay(
                    1,
                    JSON.stringify({
                        content: params.content,
                        callback: "payCallback"
                    })
                );
            },
        },

        /**
         * ios
         * @constructor
         */
        ios: {
            /**
             * 
             * @param params
             */
            wechat(params = {}) {
                window.webkit.messageHandlers.KIosWeXPayCall.postMessage({
                    content: params.content,
                    callback: "payCallback"
                });
            },

            /**
             * 
             * @param params
             */
            alipay(params = {}) {
                window.webkit.messageHandlers.KIosWeXPayCall.postMessage({
                    content: params.content,
                    callback: "payCallback"
                });
            },
        }
    }
};

export default pay



